
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { userService } from './user.service';

@Injectable({
  providedIn: 'root'
})

/** O Quardio serve para proteger as rotas, ou seja  uma questo de segurana.
 * Por exemplo : para que no haja violao de acesso caso no tenha uma autenticao.
 */
export class GuardiaoGuard implements CanActivate {


  constructor(private userServcice: userService) {

  }


  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    console.info(" Guardio sendo chamado")
      return this.userServcice.userAutenticado();
  }

}